@media screen and (min-width: 480px) {
  // .about {
  // }

  // .about__container {
  // }

  // .about__img {
  // }

  // }
  .about__picture {
    display: flex;
    justify-content: center;
  }

  // .about__title {
  // }

  .about__info {
    max-width: 280px;
    margin-left: auto;
    margin-right: auto;
  }

  .about__title {
    font-family: 'Caveat Brush';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 1.25;
    text-align: center;
    margin-top: 17px;
    margin-bottom: 8px;

    color: var(--primary-text-color);
  }

  .about__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.25;

    color: var(--primary-text-color);
  }

  .about__text--down {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.25;

    color: var(--primary-text-color);
    display: none;
  }

  .about__btn {
    font-family: 'Caveat Brush';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.28;
    text-align: center;

    color: var(--buttom-bcg-color);
    background-color: var(--white-color);
    border: none;
    display: block;
  }
}
@media screen and (min-width: 768px) {
  .about__container {
    display: flex;
    align-items: flex-end;
  }

  .about__info {
    max-width: 336px;
    margin-left: 32px;
  }

  .about__title {
    display: flex;
    justify-content: flex-start;
    margin-top: 0px;
  }
}

@media screen and (min-width: 1280px) {
  .about__text,
  .about__text--down {
    font-family: 'Montserrat';
    font-size: 18px;
    line-height: 22px;

    color: var(--primary-text-color);
  }
  .about__text {
    margin-top: 0px;
  }

  .about__text--down {
    margin-bottom: 0px;
  }

  .about__info {
    max-width: 392px;
    margin-left: 140px;
    margin-right: 0px;
    padding-bottom: 30px;
  }

  .about__title {
    font-size: 36px;
    line-height: 1.25;
    margin-bottom: 30px;
  }

  .about__text--down {
    display: block;
  }

  .about__btn {
    display: none;
  }
}
