body {
   margin: 0;

   font-family: 'Montserrat';
   font-size: 16px;
   line-height: calc(20px / 16px);
   color: #2e2520;

   @media screen and (min-width: 1280px) {
      font-size: 18px;
      line-height: calc(22px / 18px);
   }
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;

    padding-left: 16px;
    padding-right: 16px;
  }
}

.list {
   padding: 0;
   margin: 0;
   list-style: none;
}

.link {
   text-decoration: none;
   list-style: none;
}

img {
   display: block;
   max-width: 100%;
   height: auto;
}

.section {
   max-width: 280px;

  //  padding-left: 20px;
  //  padding-right: 20px;

   @media screen and (min-width: 768px) {
      max-width: 520px;

      // padding-left: 32px;
      // padding-right: 32px;
   }

   @media screen and (min-width: 1280px) {
      max-width: 1248px;

      // padding-left: 16px;
      // padding-right: 16px;
   }
}

.section-title {
   font-family: 'Caveat Brush';
   font-size: 24px;
   line-height: calc(30px / 24px);
   color: #2e2520;
   text-align: center;

   @media screen and (min-width: 768px) {
      font-size: 36px;
      line-height: calc(45px / 36px);
   }
}