.registration {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 0;
  padding-bottom: 180px;

  background-image: url(../images/registration/img-1_lg.png),
    url(../images/registration/img-2_lg.png);
  background-position: left 85px bottom 240px, top 44px right -38.74px;
  transform: rotate(69.49deg), rotate(-47.68deg);
  background-repeat: no-repeat;
}
.registration_heading {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: 'Caveat Brush';
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: calc(65 / 36);
  text-align: center;
  color: #2e2520;
}
.registration_content {
  max-width: 822px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 104px;
  background: #fffff9;
  box-shadow: -4px -4px 4px rgba(171, 129, 108, 0.15),
    4px 4px 4px rgba(171, 129, 108, 0.15);
  border-radius: 150px;
}

.registration_text {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: calc(24 / 20);
  text-align: right;

  color: #2e2520;
}

.registration_block {
  display: flex;
  flex-direction: column;
  padding-left: 32px;
  margin-right: 28px;
  margin-left: 32px;

  border-left: 1px solid rgba(171, 129, 108, 0.5);
}
.registration_title {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;

  margin-bottom: 28px;
  margin-top: 0;
}

.registration_form {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.registration_input {
  width: 238px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: calc(20 / 16);

  border: none;
  border-bottom: 1px solid #ab816c;
  outline: transparent;

  &::-moz-placeholder {
    color: rgba(171, 129, 108, 0.5);
    outline: transparent;
  }
  &:-ms-input-placeholder {
    color: rgba(171, 129, 108, 0.5);
  }
  &::-webkit-input-placeholder {
    color: rgba(171, 129, 108, 0.5);
  }
}

.registration_btn {
  font-family: 'Caveat Brush';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  text-align: center;

  color: #ffffff;
  padding: 8px 45px;
  background: #ab816c;
  border-radius: 25px;
  border: 0;
}
