.speakers{
   // max-width: 100%;

   margin-left: auto;
   margin-right: auto;

   @media screen and (min-width: 480px) {
      max-width: 280px;
   }
   @media screen and (min-width: 768px) {
      max-width: 704px;
   }
   @media screen and (min-width: 1280px) {
      background:var(--primary-bcg-color) url('../images/background/bg-speakers_lg.svg');
      background-repeat: no-repeat;
      border-radius: 40px;
      
      max-width: 1251px;
   }
}

.speakers-container {
   padding-right: 0;
   padding-left: 0;
}

.speakers_title {
   margin: 0;
}

.speakers_list {
   padding-top: 80px;
   padding-bottom: 16px;

   display: flex;
   flex-wrap: wrap;
   gap: 20px;
   @media screen and (max-width: 767px){
      background: var(--primary-bcg-color) url('../images/background/bg-speakers_sm.svg');
      background-repeat: no-repeat;
      border-radius: 20px;
      
      
   }
   
   @media screen and (min-width: 768px) and (max-width: 1279px){
      background:var(--primary-bcg-color) url('../images/background/bg-speakers_md.svg');
      
      background-repeat: no-repeat;
      border-radius: 40px;

      padding-top: 155px;
      padding-bottom: 20px;
      gap: 32px;
   }

   @media screen and (min-width: 1280px) {
      gap: 103px;
      justify-content: space-between;
      
      padding-right: 107px;
      padding-left: 107px;
      padding-top: 179px;
      padding-bottom: 20px;
   }
}

.speakers_list-item {
   width: 130px;

   @media screen and (min-width: 768px) {
      width: 152px;
   }
   @media screen and (min-width: 1280px) {
      width: 181px;
   }
}

.speakers_img {
   max-width: 100%;
   border-radius: 0%;

   padding-bottom: 12px;

   @media screen and (min-width: 768px) {
      width: 152px;
      height: 152px;

      padding-bottom: 20px;
   }
   @media screen and (min-width: 1280px) {
      width: 181px;
      height: 181px;

      padding-bottom: 20px;
   }
}

.speakers_sub-title {
   margin: 0;
   padding-bottom: 12px;

   font-style: italic;
   font-weight: 700;
   font-size: 16px;
   line-height: calc(20px / 16px);
   text-align: center;

   @media screen and (min-width: 768px) {
      font-weight: 600;
      font-size: 18px;
      line-height: calc(22px / 18px);

      padding-bottom: 20px;
   }
}

.speakers_text {
   margin: 0;
   padding-bottom: 12px;

   font-style: italic;
   font-size: 12px;
   line-height: calc(15px / 12px);
   text-align: center;

   @media screen and (min-width: 768px) {
      font-weight: 500;
      font-size: 14px;
      line-height: calc(17px / 14px);
      padding-bottom: 20px;
   }
   @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: calc(20px / 16px);
   }
}